import { useEffect, useState, useCallback, useRef } from 'react';
import { useRouter } from 'next/navigation';
import { io, Socket } from 'socket.io-client';
import { services } from '@/services/api';
import type { Notificacao } from '@/services/api/notificacao.service';
import { cookies } from '@/utils/cookies';
import toast from 'react-hot-toast';
import { useAuth } from './useAuth';
import { useBackendUrl } from './useAppConfig';

interface UseNotificationsReturn {
  notificacoes: Notificacao[];
  countNaoLidas: number;
  loading: boolean;
  isConnected: boolean;
  loadNotificacoes: () => Promise<void>;
  marcarComoLida: (id: number) => Promise<void>;
  marcarTodasComoLidas: () => Promise<void>;
  deleteNotificacao: (id: number) => Promise<void>;
}

export function useNotifications(): UseNotificationsReturn {
  const router = useRouter();
  const { user, isAuthenticated } = useAuth();
  const backendUrl = useBackendUrl();
  const [notificacoes, setNotificacoes] = useState<Notificacao[]>([]);
  const [countNaoLidas, setCountNaoLidas] = useState(0);
  const [loading, setLoading] = useState(true);
  const [socket, setSocket] = useState<Socket | null>(null);
  const [isConnected, setIsConnected] = useState(false);
  const socketInitializedRef = useRef(false);

  // Inicializar Socket.IO apenas quando autenticado
  useEffect(() => {
    const token = cookies.token.get();
    if (!token || !isAuthenticated || !user) {
      // Limpar socket se n√£o autenticado
      setSocket(prevSocket => {
        if (prevSocket) {
          prevSocket.close();
          setIsConnected(false);
        }
        return null;
      });
      socketInitializedRef.current = false;
      return;
    }

    // Evitar m√∫ltiplas inicializa√ß√µes do mesmo socket
    if (socketInitializedRef.current) {
      return;
    }

    // Usar backendUrl diretamente
    if (!backendUrl) {
      console.warn('[Socket.IO] backendUrl n√£o configurada');
      return;
    }
    
    socketInitializedRef.current = true;
    
    const newSocket = io(backendUrl, {
      auth: {
        token: token
      },
      transports: ['websocket', 'polling'],
      reconnection: true,
      reconnectionDelay: 1000,
      reconnectionAttempts: 5
    });

    newSocket.on('connect', () => {
      console.log('[Socket.IO] Conectado');
      setIsConnected(true);
    });

    newSocket.on('disconnect', () => {
      console.log('[Socket.IO] Desconectado');
      setIsConnected(false);
    });

    newSocket.on('connect_error', (error) => {
      console.error('[Socket.IO] Erro de conex√£o:', error);
      setIsConnected(false);
    });

    // Escutar novas notifica√ß√µes
    newSocket.on('notificacao:nova', (notificacao: Notificacao) => {
      console.log('[Socket.IO] Nova notifica√ß√£o recebida:', notificacao);
      setNotificacoes(prev => [notificacao, ...prev]);
      setCountNaoLidas(prev => prev + 1);
      
      // Mostrar toast de notifica√ß√£o
      toast.success(notificacao.titulo, {
        duration: 5000,
        icon: 'üîî'
      });
    });

    // Escutar atualiza√ß√µes de contador
    newSocket.on('notificacao:count', (data: { count: number }) => {
      setCountNaoLidas(data.count);
    });

    // Confirmar que notifica√ß√£o foi marcada como lida
    newSocket.on('notificacao:lida:confirmado', (data: { notificacaoId: number }) => {
      setNotificacoes(prev =>
        prev.map(n => n.id === data.notificacaoId ? { ...n, lida: true } : n)
      );
    });

    setSocket(newSocket);

    return () => {
      newSocket.close();
      socketInitializedRef.current = false;
      setIsConnected(false);
    };
  }, [router, isAuthenticated, user, backendUrl]);

  // Carregar notifica√ß√µes
  const loadNotificacoes = useCallback(async () => {
    const token = cookies.token.get();
    if (!token || !isAuthenticated || !user) {
      setLoading(false);
      return;
    }

    try {
      setLoading(true);
      const response = await services.notificacao.list({ page: 1, perPage: 20 });
      setNotificacoes(response.data || []);
      
      // Carregar contador
      const count = await services.notificacao.countNaoLidas();
      setCountNaoLidas(count || 0);
    } catch (error) {
      console.error('Erro ao carregar notifica√ß√µes:', error);
      // Em caso de erro, manter estado vazio
      setNotificacoes([]);
      setCountNaoLidas(0);
    } finally {
      setLoading(false);
    }
  }, [isAuthenticated, user]);

  // Carregar notifica√ß√µes quando autenticado
  useEffect(() => {
    if (isAuthenticated && user) {
      loadNotificacoes();
    } else {
      // Limpar notifica√ß√µes se n√£o autenticado
      setNotificacoes([]);
      setCountNaoLidas(0);
      setLoading(false);
    }
  }, [isAuthenticated, user, loadNotificacoes]);

  // Marcar como lida
  const marcarComoLida = useCallback(async (id: number) => {
    if (!isAuthenticated || !user) return;

    try {
      // Atualizar localmente primeiro (otimista)
      setNotificacoes(prev =>
        prev.map(n => n.id === id ? { ...n, lida: true } : n)
      );
      setCountNaoLidas(prev => Math.max(0, prev - 1));

      // Enviar via Socket.IO primeiro, depois via API como fallback
      if (socket?.connected) {
        socket.emit('notificacao:lida', { notificacaoId: id });
      }
      
      await services.notificacao.marcarComoLida(id);
      // Recarregar para garantir sincroniza√ß√£o
      const count = await services.notificacao.countNaoLidas();
      setCountNaoLidas(count || 0);
    } catch (error) {
      console.error('Erro ao marcar notifica√ß√£o como lida:', error);
      // Reverter mudan√ßa local recarregando
      await loadNotificacoes();
    }
  }, [socket, loadNotificacoes, isAuthenticated, user]);

  // Marcar todas como lidas
  const marcarTodasComoLidas = useCallback(async () => {
    if (!isAuthenticated || !user) return;

    try {
      setNotificacoes(prev => prev.map(n => ({ ...n, lida: true })));
      setCountNaoLidas(0);
      await services.notificacao.marcarTodasComoLidas();
      // Recarregar para garantir sincroniza√ß√£o
      await loadNotificacoes();
    } catch (error) {
      console.error('Erro ao marcar todas como lidas:', error);
      await loadNotificacoes();
    }
  }, [loadNotificacoes, isAuthenticated, user]);

  // Deletar notifica√ß√£o
  const deleteNotificacao = useCallback(async (id: number) => {
    if (!isAuthenticated || !user) return;

    try {
      const notificacao = notificacoes.find(n => n.id === id);
      setNotificacoes(prev => prev.filter(n => n.id !== id));
      if (notificacao && !notificacao.lida) {
        setCountNaoLidas(prev => Math.max(0, prev - 1));
      }
      await services.notificacao.delete(id);
      // Recarregar para garantir sincroniza√ß√£o
      await loadNotificacoes();
    } catch (error) {
      console.error('Erro ao deletar notifica√ß√£o:', error);
      await loadNotificacoes();
    }
  }, [notificacoes, loadNotificacoes, isAuthenticated, user]);

  return {
    notificacoes,
    countNaoLidas,
    loading,
    isConnected,
    loadNotificacoes,
    marcarComoLida,
    marcarTodasComoLidas,
    deleteNotificacao
  };
}

