"use client";

import React from 'react';
import { Modal } from '@/components/ui/modal';
import Button from '@/components/ui/button/Button';
import { Notificacao } from '@/services/api/notificacao.service';
import { formatDate } from '@/utils/format';
import { TrashBinIcon, CheckCircleIcon, ArrowRightIcon } from '@/icons';
import { useAuth } from '@/hooks/useAuth';
import { useRouter } from 'next/navigation';

interface NotificacaoModalProps {
  isOpen: boolean;
  onClose: () => void;
  notificacao: Notificacao | null;
  onMarcarComoLida?: (id: number) => void;
  onDelete?: (id: number) => void;
}

function formatTimeAgo(dateString: string): string {
  try {
    const date = new Date(dateString);
    const now = new Date();
    const diffInSeconds = Math.floor((now.getTime() - date.getTime()) / 1000);

    if (diffInSeconds < 60) return 'agora';
    if (diffInSeconds < 3600) return `${Math.floor(diffInSeconds / 60)} min atr√°s`;
    if (diffInSeconds < 86400) return `${Math.floor(diffInSeconds / 3600)} hr atr√°s`;
    if (diffInSeconds < 604800) return `${Math.floor(diffInSeconds / 86400)} dias atr√°s`;
    
    return formatDate(dateString);
  } catch {
    return dateString;
  }
}

function getTipoIcon(tipo: string): string {
  switch (tipo) {
    case 'VENCIMENTO_EMPRESA':
      return '‚ö†Ô∏è';
    case 'PLANO_PAGO':
      return '‚úÖ';
    case 'COBRANCA_GERADA':
      return 'üí≥';
    case 'FATURA_PAGA':
      return 'üí∞';
    case 'PARCELA_PAGA':
      return 'üíµ';
    case 'MANUAL':
      return 'üì¢';
    default:
      return 'üîî';
  }
}

function getTipoLabel(tipo: string): string {
  switch (tipo) {
    case 'VENCIMENTO_EMPRESA':
      return 'Vencimento de Empresa';
    case 'PLANO_PAGO':
      return 'Plano Pago';
    case 'COBRANCA_GERADA':
      return 'Cobran√ßa Gerada';
    case 'FATURA_PAGA':
      return 'Fatura Paga';
    case 'PARCELA_PAGA':
      return 'Parcela Paga';
    case 'MANUAL':
      return 'Manual';
    default:
      return tipo;
  }
}

export default function NotificacaoModal({
  isOpen,
  onClose,
  notificacao,
  onMarcarComoLida,
  onDelete
}: NotificacaoModalProps) {
  const { user } = useAuth();
  const router = useRouter();
  const isSuperAdmin = user?.role === 'SUPERADMIN';

  if (!notificacao) return null;

  const handleMarcarComoLida = () => {
    if (onMarcarComoLida) {
      onMarcarComoLida(notificacao.id);
    }
    onClose();
  };

  const handleDelete = () => {
    if (window.confirm('Tem certeza que deseja excluir esta notifica√ß√£o?')) {
      if (onDelete) {
        onDelete(notificacao.id);
      }
      onClose();
    }
  };

  const handleActionLink = () => {
    if (notificacao.actionLink) {
      router.push(notificacao.actionLink);
      onClose();
    }
  };

  return (
    <Modal
      isOpen={isOpen}
      onClose={onClose}
      className="max-w-[700px] m-4"
    >
      <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
        <div className="px-2 pr-14">
          {/* Header */}
          <div className="flex items-start gap-4 mb-6">
            <div className="flex-shrink-0">
              <div className="w-16 h-16 rounded-full bg-gray-100 dark:bg-gray-800 flex items-center justify-center text-3xl">
                {getTipoIcon(notificacao.tipo)}
              </div>
            </div>
            <div className="flex-1 min-w-0">
              <h4 className="text-2xl font-semibold text-gray-900 dark:text-white mb-2">
                {notificacao.titulo}
              </h4>
              <div className="flex items-center gap-2 flex-wrap mb-3">
                <span className="px-3 py-1 text-xs font-medium rounded-full bg-gray-100 dark:bg-gray-800 text-gray-700 dark:text-gray-300">
                  {getTipoLabel(notificacao.tipo)}
                </span>
                {!notificacao.lida && (
                  <span className="px-3 py-1 text-xs font-medium rounded-full bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-300">
                    N√£o lida
                  </span>
                )}
                <span className="text-xs text-gray-500 dark:text-gray-400">
                  {formatTimeAgo(notificacao.createdAt)}
                </span>
              </div>
            </div>
          </div>

          {/* Mensagem completa */}
          <div className="mb-6">
            <h5 className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Mensagem:
            </h5>
            <div className="p-4 bg-gray-50 dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700">
              <p className="text-sm leading-relaxed text-gray-700 dark:text-gray-300 whitespace-pre-wrap">
                {notificacao.mensagem}
              </p>
            </div>
          </div>

          {/* Informa√ß√µes adicionais para SUPERADMIN */}
          {isSuperAdmin && (notificacao.empresa || notificacao.user) && (
            <div className="mb-6 p-4 bg-gray-50 dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700">
              <h5 className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-3">
                Informa√ß√µes Adicionais:
              </h5>
              <div className="space-y-2 text-sm text-gray-600 dark:text-gray-400">
                {notificacao.empresa && (
                  <div className="flex items-center gap-2">
                    <span className="text-lg">üì¶</span>
                    <span className="font-medium">Empresa:</span>
                    <span>{notificacao.empresa.nome}</span>
                  </div>
                )}
                {notificacao.user && (
                  <div className="flex items-center gap-2">
                    <span className="text-lg">üë§</span>
                    <span className="font-medium">Usu√°rio:</span>
                    <span>{notificacao.user.nome}</span>
                    {notificacao.user.email && (
                      <span className="text-gray-500 dark:text-gray-500">
                        ({notificacao.user.email})
                      </span>
                    )}
                  </div>
                )}
              </div>
            </div>
          )}

          {/* Data completa */}
          <div className="mb-6 text-xs text-gray-500 dark:text-gray-400">
            <p>
              <span className="font-medium">Criada em:</span>{' '}
              {formatDate(notificacao.createdAt)}
            </p>
            {notificacao.updatedAt !== notificacao.createdAt && (
              <p className="mt-1">
                <span className="font-medium">Atualizada em:</span>{' '}
                {formatDate(notificacao.updatedAt)}
              </p>
            )}
          </div>

          {/* Link de a√ß√£o */}
          {notificacao.actionLink && notificacao.actionLabel && (
            <div className="mb-6">
              <Button
                variant="primary"
                onClick={handleActionLink}
                startIcon={<ArrowRightIcon className="w-5 h-5" />}
                className="w-full sm:w-auto"
                aria-label={notificacao.actionLabel}
              >
                {notificacao.actionLabel}
              </Button>
            </div>
          )}

          {/* Bot√µes de a√ß√£o */}
          <div className="flex flex-wrap items-center justify-end gap-3 pt-6 border-t border-gray-200 dark:border-gray-700">
            {!notificacao.lida && onMarcarComoLida && (
              <Button
                variant="outline"
                onClick={handleMarcarComoLida}
                startIcon={<CheckCircleIcon className="w-5 h-5" />}
                aria-label="Marcar como lida"
              >
                Marcar como lida
              </Button>
            )}
            {onDelete && (
              <Button
                variant="outline"
                onClick={handleDelete}
                startIcon={<TrashBinIcon className="w-5 h-5" />}
                aria-label="Excluir notifica√ß√£o"
                className="text-red-600 hover:text-red-700 dark:text-red-400 dark:hover:text-red-300"
              >
                Excluir
              </Button>
            )}
            <Button
              variant="outline"
              onClick={onClose}
              aria-label="Fechar"
            >
              Fechar
            </Button>
          </div>
        </div>
      </div>
    </Modal>
  );
}

